.app {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    padding: 30px;
    border-radius: 25px;
    color: #322d4a;

}
img {
    width: 50%;
}
.create-task {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 70px;
    margin: 0px 30px;
}
.gestor-list {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.create-task h2 {
    text-align: center;
}

@media (min-width: 768px) {
    .container {
        width: 80%;
    }
}
.button-add {
    position: absolute;
    right: 0;
    top: 89%;
}