.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    background-color: #fff;
    padding: 10px;
    border-radius: 10px;
    color: #322d4a;
}
textarea {
    padding: 5px;
    width: 280px;
    height: 50px;
    border: 0px;
    border-radius: 10px;
    resize: none;
    outline: none;
    color: #fff;
    background-color: #322d4a;
    -webkit-box-shadow: 0px 0px 13px 2px rgba(133,222,207,1);
    -moz-box-shadow: 0px 0px 13px 2px rgba(133,222,207,1);
    box-shadow: 0px 0px 13px 2px rgba(133,222,207,1);
}
.text-label {
    font-size: 20px;
    margin: 10px 0px;
}