.list-task {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;
    align-items: center;
    border-radius: 10px;
    margin: 40px 0px;
    padding: 0px 15px;
    color: #fff;
    background-color: #322d4a;
    -webkit-box-shadow: 0px 0px 13px 2px rgba(133,222,207,1);
    -moz-box-shadow: 0px 0px 13px 2px rgba(133,222,207,1);
    box-shadow: 0px 0px 13px 2px rgba(133,222,207,1);
}
.icon svg {
    width: 30px;
    height: 30px;
}
.Icon-check-active svg {
    color: green;
}
.TodoItem-p-complete {
    text-decoration: line-through #fff;
}